.device-holder {
    .image-holder {
        position: relative;
        img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        }
        .image-caption {
            position: absolute;
            top: 0;
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 80px 5%;
            color: #fff;
            background: #13616399;
            .caption-head {
                font-size: 30px;
                font-weight: 300;
                line-height: 35px;
                h2 {
                    font-size: 50px;
                    font-weight: 300;
                    line-height: 60px;
                    margin-bottom: 0;
                }
            }
            .caption-footer {
                text-align: center;
                max-width: 300px;
                margin: 0 auto;
                width: 100%;
                .new-user {
                    color: #fff;
                    text-decoration: none;
                    margin-bottom: 15px;
                }
            }
        }
    }
    .login-content {
        max-width: 600px;
        width: 100%;
        padding: 0 30px;
        position: relative;
        .login-holder {
            max-width: 330px;
            width: 100%;
            h2 {
                color: #1C4F51;
                font-weight: normal;
                margin-bottom: 25px;
                text-align: center;
                font-size: 40px;
                font-weight: 300px;
            }
        }
        .modal {
            position: absolute;
            overflow-y: hidden;
            .modal-holder {
                max-width: 417px;
                margin: 30% auto;
                .modal-content {
                     border-radius: 12px;
                     box-shadow: 0px 12px 17px #00000024, 0px 5px 22px #0000001f, 0px 7px 8px #00000033;
                    .modal-body {
                       max-width: 413px;
                        padding: 40px;
                        .modal-title {
                            color: rgba(5, 36, 37, 0.87);
                            font-weight: normal;
                            font-size: 34px;
                            margin-bottom: 15px;
                            text-align: center;
                        }
                        .modal-details {
                            max-width: 335px;
                            text-align: center;
                            color: rgba(5, 36, 37, 0.87);
                            margin-bottom: 30px;
                        }
                    }
                }
                
            }
        }
    }
}
